a {
  text-decoration: none;
  color: #f8f8f2;
}

.carousel-root {
  width: 70%;
}

.carousel .thumb.selected,
.carousel .thumb:hover {
  border: 3px solid #ccc !important;
}

.carousel .thumb {
  border: 3px solid #555 !important;
}

.carousel .slide .legend {
  background-color: #44475a !important;
}

.contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: 1rem;
  margin-right: 1rem;
}

.contact > p {
  width: 100%;
}

.contact .item {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 3rem;
  font-family: "Nunito";
}

.contact .item > a {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: -1.7rem;
  margin-top: -22px;
}

.contact .item > p {
  margin-top: 0.5rem;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.experience {
  max-width: 90%;
}

.experience-wrapper {
  margin-bottom: 100px;
}

.introduction {
  display: flex;
  align-items: center;
  width: 85%;
  margin-top: 40px;
}

.introduction > img {
  max-width: 100%;
}

.introduction .text {
  margin-left: 3.5rem;
}

.introduction .title {
  margin-left: 2.5rem;
  font-size: 2rem;
}

.knowledge {
  max-width: 90%;
}

.knowledge > p {
  font-family: "Nunito";
  font-weight: bold;
  font-size: 1.8rem;
  text-align: center;
  margin-top: 2rem;
}

.languages {
  width: 10rem;
  display: flex;
  align-items: center;
}

.logo {
  height: 5rem;
  width: 5rem;
  object-fit: contain;
}

.logo-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  width: 7.5rem;
  height: 10.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
}

.logo-container > p {
  visibility: none;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s;
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Nunito";
  font-weight: 300;
  font-size: 0.9rem;
  text-align: center;
}

.logo-container:hover > p {
  visibility: visible;
  opacity: 1;
}

.logo-large {
  max-width: 5rem;
}

.logo-group {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.logo-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background-color: #44475a;
  height: 7.5rem;
  width: 7.5rem;
  display: flex;
  border-radius: 1rem;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.logo-wrapper:hover {
  opacity: 0.7;
  height: 8rem;
  width: 8rem;
}

.main {
  background-color: #272c34;
  color: #f8f8f2;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.navbar {
  width: 100%;
  height: 4rem;
  background-color: #44475a;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar > h2 {
  margin-left: 2rem;
}

.navbar .options {
  width: 50%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar .options > div {
  width: 100%;
  height: 1.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #f8f8f2;
}

.navbar .options > div:last-of-type {
  border-right: none;
}

.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 90%;
}

.resume {
  margin-top: 100px;
  margin-bottom: 50px;
  text-align: center;
}

.resume a {
  height: 50px;
  padding: 0 40px;
  border-radius: 25px;
  color: white;
  background-color: #44475a;
  border: none;
  cursor: pointer;
  transition: 0.3s;
  font-family: "Nunito";
  font-weight: bolder;
  font-size: xx-large;
}

.resume a:hover {
  background-color: rgba(68, 71, 90, 0.7);
}

.text {
  font-size: 1.5rem;
  font-family: "Nunito";
  word-break: break-word;
}

.text a:hover {
  text-decoration: underline;
}

.title {
  text-align: center;
  font-size: 2.5rem;
  font-weight: bold;
}

@media (max-width: 800px) {
  .carousel-root {
    width: 100%;
  }

  .navbar > h2 {
    margin-left: 1.5rem;
    margin-right: 0.5rem;
  }

  .introduction {
    flex-wrap: wrap;
  }
  .introduction .text {
    margin-left: 0;
  }

  .contact {
    flex-direction: column;
  }
}
